import React, { useMemo, useState } from 'react';
import {
  Typography,
  AppBar,
  Toolbar,
  IconButton,
  Button,
  SwipeableDrawer,
  List,
  ListItem,
  ListItemText,
  //   Link,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Menu as MenuIcon } from '@material-ui/icons';
import { Link as RouterLink, withRouter } from 'react-router-dom';
import { useMediaQuery } from '@material-ui/core';

import theme from '../../theme';
import SearchBar from './SearchBar';

const useStyles = makeStyles((theme) => ({
  logoContainer: { flexGrow: 1 },
  logo: {},

  navigationItemsDesktop: {
    display: 'flex',
    flexDirection: 'row',
  },
  navigationItemsMobile: {
    display: 'flex',
    flexDirection: 'column',
  },
  publicItems: { display: 'flex' },
  accountItems: { display: 'flex', marginLeft: 'auto' },
  offset: theme.mixins.toolbar,
}));

const NavBar = withRouter(({ history }, props) => {
  const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
  const isSmallDevice = useMediaQuery((theme) => theme.breakpoints.down('sm'));
  const classes = useStyles();
  // const [menuIsHide, setMenuIsHide] = useState(matches);
  const [drawerIsOpen, setDrawerIsOpen] = useState(false);
  const [linkItemList, setLinkItemList] = useState([
    { name: 'Latest', label: 'latest', type: 'public' },
    { name: 'Features', label: 'features', type: 'public' },
    { name: 'Regions', label: 'regions', type: 'public' },
    { name: 'Account', label: 'account', type: 'account' },
    { name: 'Login', label: 'login', type: 'account' },
  ]);

  const isActive = useMemo(
    () => (history, path) => {
      if (history.location.pathname === path)
        return {
          color: prefersDarkMode
            ? theme.palette.secondary.dark
            : theme.palette.secondary.light,
        };
      else return { color: '#ffffff' };
    },
    [prefersDarkMode]
  );

  const toggleDrawer = () => {
    setDrawerIsOpen(!drawerIsOpen);
  };

  const NavItems = (props) =>
    props.isMobile
      ? props.linkItemList.map((linkItem) => (
          <List className={classes.publicItemList}>
            <div className={classes.publicItems}>
              <ListItem
                button
                component={RouterLink}
                to={`/${linkItem.label}`}
                color="inherit"
                style={isActive(history, `/${linkItem.label}`)}
                key={`${linkItem.label}`}
              >
                <ListItemText primary={`${linkItem.name}`} />
              </ListItem>
            </div>
          </List>
        ))
      : props.linkItemList.map((linkItem) => (
          <Button
            component={RouterLink}
            to={`/${linkItem.label}`}
            color="inherit"
            style={isActive(history, `/${linkItem.label}`)}
            key={`${linkItem.label}`}
          >
            <Typography>{linkItem.name}</Typography>
          </Button>
        ));

  return (
    <div>
      <AppBar position="fixed">
        <Toolbar>
          {isSmallDevice ? (
            <>
              <IconButton
                edge={false}
                aria-label="menu"
                color="inherit"
                onClick={toggleDrawer}
              >
                <MenuIcon />
                <SwipeableDrawer
                  anchor={theme.direction === 'rtl' ? 'right' : 'left'}
                  onClose={toggleDrawer}
                  open={drawerIsOpen}
                  onOpen={toggleDrawer}
                >
                  <NavItems
                    linkItemList={linkItemList}
                    className={classes.navigationItemsMobile}
                    isMobile={true}
                  />
                </SwipeableDrawer>
              </IconButton>
              <Button
                component={RouterLink}
                to="/"
                style={isActive(history, '/')}
                className={classes.logo}
                color="inherit"
              >
                <Typography variant="h6">Initcard</Typography>
              </Button>
              <SearchBar />
            </>
          ) : (
            <>
              <div className={classes.logoContainer}>
                <Button
                  component={RouterLink}
                  to="/"
                  style={isActive(history, '/')}
                  className={classes.logo}
                  color="inherit"
                >
                  <Typography variant="h6">Initcard</Typography>
                </Button>
              </div>
              <SearchBar />
              <NavItems
                linkItemList={linkItemList}
                className={classes.navigationItemsDesktop}
                isMobile={false}
              />
            </>
          )}
        </Toolbar>
      </AppBar>
      <div className={classes.offset} />
    </div>
  );
});

export default NavBar;
