import React, { useState } from 'react';
import { Grid } from '@material-ui/core';

import CardMain from '../Card/CardMain';
import { SingleItem } from '../Demo/SingleItem';

const Home = (props) => {
  const [cardData, setCardData] = useState(SingleItem);
  const [cardIsExpanded, setCardIsExpanded] = useState(true);

  return (
    <Grid container direction="column" justify="flex-start" alignItems="center">
      <Grid item>
        <CardMain cardData={cardData} cardIsExpanded={cardIsExpanded} />
      </Grid>
    </Grid>
  );
};

export default Home;
