import React, { useState } from 'react';
import QRCode from 'react-qr-code';
import {
  Card,
  CardHeader,
  Popper,
  ClickAwayListener,
  CardMedia,
  CardContent,
  CardActions,
  Collapse,
  Typography,
  IconButton,
  Divider,
  Avatar,
  Dialog,
  DialogTitle,
  DialogContent,
  Link,
  Paper,
} from '@material-ui/core';
import { makeStyles, withStyles, styled } from '@material-ui/core/styles';
import {
  MoreHoriz as MoreHorizIcon,
  Language as LanguageIcon,
  Bookmark as BookmarkIcon,
  Share as ShareIcon,
  ExpandMore as ExpandMoreIcon,
  Phone as PhoneIcon,
  Email as EmailIcon,
  Message as MessageIcon,
  QrCode2 as QrCode2Icon,
  Cancel as CancelIcon,
  AddCircle as AddCircleIcon,
} from '@material-ui/icons';
import { Link as RouterLink } from 'react-router-dom';
import clsx from 'clsx';

import CardDisplayWrapper from './CardDisplayWrapper';
import CardMoreOptions from './CardMoreOptions';
import CardChips from './CardChips';
import CardLinks from './CardLinks';
import CardDescriptionList from './CardDescriptionList';

const useStyles = makeStyles((theme) => ({
  cardSize: {
    maxWidth: 600,
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  cardContentContainer: {
    padding: '0',
  },
}));

const styles = (theme) => ({
  dialogCloseButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
  },
  dialogAddButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
  },
  moreOptionsContainer: {
  },
});

const CustomDialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, onAdd, ...rest } = props;
  return (
    <DialogTitle disableTypography {...rest}>
      <Typography variant="h6" component="div">
        {children}
      </Typography>
      {onAdd ? (
        <IconButton
          aria-label="close"
          onClick={onAdd}
          className={classes.dialogAddButton}
        >
          <AddCircleIcon />
        </IconButton>
      ) : null}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          className={classes.dialogCloseButton}
        >
          <CancelIcon />
        </IconButton>
      ) : null}
    </DialogTitle>
  );
});

const CustomPopper = styled(Popper)({
  position: 'absolute',
});

const MoreOptionsContainer = withStyles(styles)(
  React.forwardRef((props, ref) => {
    const { classes, children, ...rest } = props;
    return (
      <Paper className={classes.moreOptionsContainer} {...rest}>
        {children}
      </Paper>
    );
  })
);

const MoreOptionsOnClick = (props) => {
  const {
    moreOptionsToggleHandler,
    popperId,
    moreOptions,
    moreOptionsIsOpen,
    anchorEl,
  } = props;
  return (
    <>
      <IconButton
        aria-describedby={popperId}
        onClick={moreOptionsToggleHandler}
        ref={anchorEl}
        disabled={moreOptionsIsOpen}
      >
        <MoreHorizIcon />
      </IconButton>
      <ClickAwayListener
        mouseEvent="onMouseDown"
        touchEvent="onTouchStart"
        onClickAway={moreOptionsToggleHandler}
      >
        <CustomPopper
          id={popperId}
          open={moreOptionsIsOpen}
          anchorEl={anchorEl}
          popperRef={anchorEl}
          placement="bottom-end"
          modifiers={[
            { name: 'flip', enabled: false },
            {
              name: 'preventOverflow',
              enabled: false,
            },
          ]}
        >
          <MoreOptionsContainer>
            <CardMoreOptions
              moreOptions={moreOptions}
              moreOptionsHandler={moreOptionsToggleHandler}
            />
          </MoreOptionsContainer>
        </CustomPopper>
      </ClickAwayListener>
    </>
  );
};

const CardMain = (props) => {
  const [moreOptions, setMoreOptions] = useState([
    { label: 'hide', title: 'Hide', description: 'Show Hide Options' },
    {
      label: 'block',
      title: 'Block',
      description: 'Block the selected person',
    },
    { label: 'report', title: 'Report', description: 'Show Report Options' },
  ]);

  const [cardDisplayType, setCardDisplayType] = useState('3d');
  const [expanded, setExpanded] = useState(props.cardIsExpanded);
  const [moreOptionsIsOpen, setMoreOptionsIsOpen] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const [bookmarkIsOpen, setBookmarkIsOpen] = useState(false);
  const [shareIsOpen, setShareIsOpen] = useState(false);
  const [qrCodeIsOpen, setQRCodeIsOpen] = useState(false);

  const classes = useStyles();

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  const moreOptionsToggleHandler = (event) => {
    setMoreOptionsIsOpen((prev) => !prev);
    setAnchorEl(anchorEl ? null : event.currentTarget);
  };

  const bookmarkHandler = () => {
    setBookmarkIsOpen((prev) => !prev);
  };

  const shareHandler = () => {
    setShareIsOpen((prev) => !prev);
  };

  const qrCodeHandler = () => {
    setQRCodeIsOpen((prev) => !prev);
  };

  const bookmarkAddHandler = () => {};

  const popperId = moreOptionsIsOpen ? 'more-options-popper' : undefined;

  return (
    <Card className={classes.cardSize}>
      <CardHeader
        title={props.cardData.name}
        subheader={props.cardData.companyName}
        avatar={
          <RouterLink to={'/account'}>
            <Avatar src={props.cardData.users.image} />
          </RouterLink>
        }
        action={
          <MoreOptionsOnClick
            moreOptions={moreOptions}
            popperId={popperId}
            moreOptionsToggleHandler={moreOptionsToggleHandler}
            moreOptionsIsOpen={moreOptionsIsOpen}
            anchorEl={anchorEl}
          />
        }
      ></CardHeader>

      <CardMedia>
        <CardDisplayWrapper
          data={props.cardData}
          cardDisplayType={cardDisplayType}
        />
      </CardMedia>
      <CardContent className={classes.cardContentContainer}>
        <CardChips data={props.cardData} />
      </CardContent>
      <Divider light />
      <CardContent>
        <CardDescriptionList details={props.cardData.details} />
      </CardContent>
      <CardActions disableSpacing>
        <Link
          href={props.cardData.website}
          underline="none"
          target="_blank"
          rel="noopener"
          component="a"
        >
          <IconButton aria-label="website">
            <LanguageIcon />
          </IconButton>
        </Link>
        <IconButton aria-label="bookmark" onClick={bookmarkHandler}>
          <BookmarkIcon />
        </IconButton>
        <Dialog
          open={bookmarkIsOpen}
          aria-labelledby="bookmark-dialog-title"
          onClose={bookmarkHandler}
        >
          <CustomDialogTitle
            id="bookmark-dialog-title"
            onClose={bookmarkHandler}
            onAdd={bookmarkAddHandler}
          >
            Bookmark
          </CustomDialogTitle>
          <DialogContent dividers>
            <QRCode value={props.cardData.initLink} />
          </DialogContent>
        </Dialog>
        <Link
          href={`tel:+${props.cardData.contactPerson.contactNumber}`}
          underline="none"
          target="_self"
          rel="noopener"
          component="a"
        >
          <IconButton aria-label="phone">
            <PhoneIcon />
          </IconButton>
        </Link>
        <Link
          href={`mailto:${props.cardData.contactPerson.contactEmail}`}
          underline="none"
          target="_self"
          rel="noopener"
          component="a"
        >
          <IconButton aria-label="email">
            <EmailIcon />
          </IconButton>
        </Link>
        <Link
          href={`sms:${props.cardData.contactPerson.contactNumber}`}
          underline="none"
          target="_self"
          rel="noopener"
          component="a"
        >
          <IconButton aria-label="message">
            <MessageIcon />
          </IconButton>
        </Link>
        <IconButton aria-label="share" onClick={shareHandler}>
          <ShareIcon />
        </IconButton>
        <Dialog
          open={shareIsOpen}
          aria-labelledby="share-dialog-title"
          onClose={shareHandler}
        >
          <CustomDialogTitle id="share-dialog-title" onClose={shareHandler}>
            Share
          </CustomDialogTitle>
          <DialogContent dividers>
            <QRCode value={props.cardData.initLink} />
          </DialogContent>
        </Dialog>
        <IconButton aria-label="qr-code" onClick={qrCodeHandler}>
          <QrCode2Icon />
        </IconButton>
        <Dialog
          open={qrCodeIsOpen}
          aria-labelledby="qr-code-dialog-title"
          onClose={qrCodeHandler}
        >
          <CustomDialogTitle id="qr-code-dialog-title" onClose={qrCodeHandler}>
            QR Code
          </CustomDialogTitle>
          <DialogContent dividers>
            <QRCode value={props.cardData.initLink} />
          </DialogContent>
        </Dialog>
        <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ExpandMoreIcon />
        </IconButton>
      </CardActions>
      <Collapse in={expanded} timeout="auto" unmountOnExit>
        <CardContent>
          <CardLinks data={props.cardData} />
        </CardContent>
      </Collapse>
    </Card>
  );
};

export default CardMain;
